import { settings } from './modules/settings.js';

const buildGrid = config => {
	const theBox = document.createElement('div');
	theBox.classList.add('box');
	theBox.id = `box${config.boxNumber}`;

	for (let i = 0; i < config.size; i++) {
		for (let j = 0; j < config.size; j++) {
			const cell = document.createElement('div');
			cell.classList.add('cell', 'state0');
			cell.dataset.state = '0';
			cell.dataset.row = i;
			cell.dataset.col = j;
			cell.dataset.rc = `${i}-${j}`;
			theBox.append(cell);
		}
	}

	return theBox;
};

const buildPuzzle = () => {
	const { labels, items } = settings;
	const config = {
		boxNumber: 1,
		// categories: settings.categories,
		size: settings.size,
		label0: labels[0],
		items0: items[0],
		label1: labels[1],
		items1: items[1],
	};
	const firstGrid = buildGrid(config);
};

const init = () => {
	console.log('JS loaded');

	buildPuzzle();
};

window.addEventListener('load', init);
